import { Telegraf, Context, Markup } from 'telegraf';
import { createClient } from '@supabase/supabase-js';
import { ethers } from 'ethers';
import type { Database } from '@/types/supabase';

// ÁéØÂ¢ÉÂèòÈáè
const TELEGRAM_BOT_TOKEN = process.env.TELEGRAM_BOT_TOKEN!;
const SUPABASE_URL = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const SUPABASE_SERVICE_KEY = process.env.SUPABASE_SERVICE_ROLE_KEY!;
const SM_TOKEN_EXCHANGE_ADDRESS = process.env.NEXT_PUBLIC_SM_TOKEN_EXCHANGE_ADDRESS!;
const RPC_URL = process.env.NEXT_PUBLIC_RPC_URL!;

// ÂàùÂßãÂåñÂÆ¢Êà∑Á´Ø
const supabase = createClient<Database>(SUPABASE_URL, SUPABASE_SERVICE_KEY);
const provider = new ethers.JsonRpcProvider(RPC_URL);

// ÂêàÁ∫¶ ABI
const SM_TOKEN_EXCHANGE_ABI = [
  'function getUserDailyRewardInfo(address user) view returns (uint256 smBalance, uint256 dailyFlowers, bool canClaim, uint256 nextClaimTime, uint256 totalClaimed)',
  'function getDailyRewardConfig() view returns (uint256 flowersPer500Sm, uint256 maxDailyFlowersPerUser, uint256 dailyClaimInterval)',
  'function claimDailyFlowers() external'
];

// ÂàõÂª∫ÂêàÁ∫¶ÂÆû‰æã
const smTokenExchange = new ethers.Contract(SM_TOKEN_EXCHANGE_ADDRESS, SM_TOKEN_EXCHANGE_ABI, provider);

// Êâ©Â±ï Context Á±ªÂûã
interface BotContext extends Context {
  session?: {
    walletAddress?: string;
    userId?: string;
  };
}

// ÂàõÂª∫ Bot ÂÆû‰æã
export const dailyRewardBot = new Telegraf<BotContext>(TELEGRAM_BOT_TOKEN);

// ËæÖÂä©ÂáΩÊï∞ÔºöÊ†ºÂºèÂåñÊï∞Â≠ó
function formatNumber(num: number): string {
  if (num >= 1e9) return (num / 1e9).toFixed(2) + 'B';
  if (num >= 1e6) return (num / 1e6).toFixed(2) + 'M';
  if (num >= 1e3) return (num / 1e3).toFixed(2) + 'K';
  return num.toFixed(2);
}

// ËæÖÂä©ÂáΩÊï∞ÔºöÊ†ºÂºèÂåñÊó∂Èó¥
function formatTimeRemaining(seconds: number): string {
  if (seconds <= 0) return 'Áé∞Âú®ÂèØ‰ª•È¢ÜÂèñ';
  
  const hours = Math.floor(seconds / 3600);
  const minutes = Math.floor((seconds % 3600) / 60);
  
  if (hours > 0) return `${hours}Â∞èÊó∂ ${minutes}ÂàÜÈíü`;
  return `${minutes}ÂàÜÈíü`;
}

// ËæÖÂä©ÂáΩÊï∞ÔºöËé∑ÂèñÁî®Êà∑Èí±ÂåÖÂú∞ÂùÄ
async function getUserWallet(telegramUsername: string): Promise<string | null> {
  const { data: user } = await supabase
    .from('user_profiles')
    .select('wallet_address')
    .eq('telegram_username', telegramUsername)
    .single();
  
  return user?.wallet_address || null;
}

// ÂëΩ‰ª§Ôºö/daily - Êü•ÁúãÊØèÊó•Â•ñÂä±Áä∂ÊÄÅ
dailyRewardBot.command('daily', async (ctx) => {
  try {
    const telegramUsername = ctx.from?.username;
    if (!telegramUsername) {
      await ctx.reply('‚ùå ËØ∑ÂÖàËÆæÁΩÆ Telegram Áî®Êà∑Âêç');
      return;
    }
    
    const walletAddress = await getUserWallet(telegramUsername);
    if (!walletAddress) {
      await ctx.reply('‚ùå ËØ∑ÂÖàÂú® SocioMint ÁΩëÁ´ôÁªëÂÆöÊÇ®ÁöÑÈí±ÂåÖÂú∞ÂùÄ', {
        reply_markup: {
          inline_keyboard: [
            [{ text: 'üîó ÁªëÂÆöÈí±ÂåÖ', url: 'https://sociomint.com/profile' }]
          ]
        }
      });
      return;
    }
    
    // Ëé∑ÂèñÁî®Êà∑ÊØèÊó•Â•ñÂä±‰ø°ÊÅØ
    const rewardInfo = await smTokenExchange.getUserDailyRewardInfo(walletAddress);
    const [smBalance, dailyFlowers, canClaim, nextClaimTime, totalClaimed] = rewardInfo;
    
    const smBalanceFormatted = parseFloat(ethers.formatEther(smBalance));
    const dailyFlowersNum = Number(dailyFlowers);
    const nextClaimTimeNum = Number(nextClaimTime);
    const totalClaimedNum = Number(totalClaimed);
    
    // ËÆ°ÁÆóÂÄíËÆ°Êó∂
    const now = Math.floor(Date.now() / 1000);
    const remaining = nextClaimTimeNum - now;
    
    // Ê£ÄÊü•Êï∞ÊçÆÂ∫ì‰∏≠ÊòØÂê¶Â∑≤È¢ÜÂèñ
    const { data: dbCanClaim } = await supabase.rpc('can_claim_daily_reward', {
      p_user_wallet: walletAddress,
      p_claim_date: new Date().toISOString().split('T')[0]
    });
    
    const actualCanClaim = canClaim && dbCanClaim;
    
    const statusMessage = `
üå∏ **ÊØèÊó•ÊåÅÂ∏ÅÂ•ñÂä±Áä∂ÊÄÅ**

üí∞ **SM ‰ΩôÈ¢ù**: ${formatNumber(smBalanceFormatted)} SM
üå∫ **‰ªäÊó•ÂèØÈ¢ÜÂèñ**: ${dailyFlowersNum} Â∞èÁ∫¢Ëä±
üìä **Á¥ØËÆ°Â∑≤È¢ÜÂèñ**: ${formatNumber(totalClaimedNum)} Â∞èÁ∫¢Ëä±

‚è∞ **Áä∂ÊÄÅ**: ${actualCanClaim ? '‚úÖ ÂèØ‰ª•È¢ÜÂèñ' : '‚è≥ ' + formatTimeRemaining(remaining)}

üí° **Â•ñÂä±ËßÑÂàô**: ÊØèÊåÅÊúâ 500 SM ‰ª£Â∏ÅÂèØËé∑Âæó 10 ÊúµÂ∞èÁ∫¢Ëä±
üìà **ÊúÄÂ§ßÂ•ñÂä±**: ÊØèÊó•ÊúÄÂ§ö 200 ÊúµÂ∞èÁ∫¢Ëä±ÔºàÈúÄÊåÅÊúâ 10,000 SMÔºâ
`;

    const keyboard = [];
    
    if (actualCanClaim && dailyFlowersNum > 0) {
      keyboard.push([{ text: 'üå∏ È¢ÜÂèñÂ•ñÂä±', callback_data: 'claim_daily_reward' }]);
    }
    
    keyboard.push(
      [{ text: 'üìä Êü•ÁúãÊéíË°åÊ¶ú', callback_data: 'daily_leaderboard' }],
      [{ text: 'üìà ÊàëÁöÑÁªüËÆ°', callback_data: 'my_daily_stats' }],
      [{ text: 'üîÑ Âà∑Êñ∞Áä∂ÊÄÅ', callback_data: 'refresh_daily_status' }]
    );
    
    await ctx.reply(statusMessage, {
      parse_mode: 'Markdown',
      reply_markup: {
        inline_keyboard: keyboard
      }
    });
    
  } catch (error) {
    console.error('Daily status error:', error);
    await ctx.reply('‚ùå Ëé∑ÂèñÊØèÊó•Â•ñÂä±Áä∂ÊÄÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
  }
});

