// ÂÆöÊó∂Êé®ÈÄÅÔºöÊØèÊó•ÊéíË°åÊ¶úÔºàÊØèÂ§©Êôö‰∏ä8ÁÇπÔºâ
export async function sendDailyLeaderboardNotification() {
  try {
    // Ëé∑ÂèñÊâÄÊúâÁªëÂÆö Telegram ÁöÑÁî®Êà∑
    const { data: users } = await supabase
      .from('user_profiles')
      .select('telegram_username')
      .not('telegram_username', 'is', null);
    
    if (!users || users.length === 0) return;
    
    // Ëé∑Âèñ‰ªäÊó•ÊéíË°åÊ¶ú Top 10
    const { data: leaderboard } = await supabase
      .from('current_daily_leaderboard')
      .select('*')
      .order('rank', { ascending: true })
      .limit(10);
    
    if (!leaderboard || leaderboard.length === 0) return;
    
    // Ëé∑Âèñ‰ªäÊó•ÁªüËÆ°
    const { data: todayStats } = await supabase
      .from('daily_reward_overview')
      .select('*')
      .eq('claim_date', new Date().toISOString().split('T')[0])
      .single();
    
    const message = `
üèÜ **‰ªäÊó•ÊØèÊó•Â•ñÂä±ÊéíË°åÊ¶ú**

${leaderboard.map((entry, index) => {
  const medal = index === 0 ? 'ü•á' : index === 1 ? 'ü•à' : index === 2 ? 'ü•â' : `${index + 1}.`;
  const username = entry.username || 'Anonymous';
  const flowers = formatNumber(parseFloat(entry.flowers_claimed || '0'));
  return `${medal} ${username}: ${flowers} üå∏`;
}).join('\n')}

üìä **‰ªäÊó•ÁªüËÆ°**
üë• ÂèÇ‰∏é‰∫∫Êï∞: ${todayStats?.total_claimers || 0}
üå∏ ÊÄªÂèëÊîæ: ${formatNumber(parseFloat(todayStats?.total_flowers_distributed || '0'))} Â∞èÁ∫¢Ëä±
üìà Âπ≥ÂùáÂ•ñÂä±: ${parseFloat(todayStats?.average_flowers_per_user || '0').toFixed(1)} Â∞èÁ∫¢Ëä±

üéØ ÊòéÂ§©ÁªßÁª≠ÊåÅÊúâ SM ‰ª£Â∏ÅÔºåËé∑ÂæóÊõ¥Â§öÂ•ñÂä±ÔºÅ
`;

    // ÂèëÈÄÅÁªôÂâç100‰∏™Áî®Êà∑ÔºàÈÅøÂÖçÈ¢ëÁéáÈôêÂà∂Ôºâ
    for (const user of users.slice(0, 100)) {
      try {
        await dailyRewardBot.telegram.sendMessage(user.telegram_username, message, {
          parse_mode: 'Markdown',
          reply_markup: {
            inline_keyboard: [
              [{ text: 'üå∏ Êü•ÁúãÊàëÁöÑÁä∂ÊÄÅ', callback_data: 'refresh_daily_status' }],
              [{ text: 'üí∞ Ë¥≠‰π∞ SM ‰ª£Â∏Å', url: 'https://sociomint.com/exchange' }]
            ]
          }
        });
        await new Promise(resolve => setTimeout(resolve, 100)); // ÈÅøÂÖçÈ¢ëÁéáÈôêÂà∂
      } catch (error) {
        console.error(`ÂèëÈÄÅÊ∂àÊÅØÁªô ${user.telegram_username} Â§±Ë¥•:`, error);
      }
    }
    
  } catch (error) {
    console.error('ÂèëÈÄÅÊØèÊó•ÊéíË°åÊ¶úÂ§±Ë¥•:', error);
  }
}

// ÂÆöÊó∂Êé®ÈÄÅÔºöÊèêÈÜíÁî®Êà∑È¢ÜÂèñÂ•ñÂä±ÔºàÊØèÂ§©‰∏äÂçà10ÁÇπÔºâ
export async function sendDailyRewardReminder() {
  try {
    // Ëé∑ÂèñÊâÄÊúâÁªëÂÆö Telegram ‰∏îÊåÅÊúâ SM ‰ª£Â∏ÅÁöÑÁî®Êà∑
    const { data: users } = await supabase
      .from('user_profiles')
      .select('telegram_username, wallet_address')
      .not('telegram_username', 'is', null);
    
    if (!users || users.length === 0) return;
    
    for (const user of users.slice(0, 50)) { // ÈôêÂà∂Êï∞Èáè
      try {
        // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÂèØ‰ª•È¢ÜÂèñÂ•ñÂä±
        const { data: canClaim } = await supabase.rpc('can_claim_daily_reward', {
          p_user_wallet: user.wallet_address,
          p_claim_date: new Date().toISOString().split('T')[0]
        });
        
        if (!canClaim) continue; // Â∑≤È¢ÜÂèñÔºåË∑≥Ëøá
        
        // Ëé∑ÂèñÁî®Êà∑Â•ñÂä±‰ø°ÊÅØ
        const rewardInfo = await smTokenExchange.getUserDailyRewardInfo(user.wallet_address);
        const dailyFlowers = Number(rewardInfo[1]);
        
        if (dailyFlowers === 0) continue; // Êó†Â•ñÂä±ÔºåË∑≥Ëøá
        
        const reminderMessage = `
üå∏ **ÊØèÊó•Â•ñÂä±ÊèêÈÜí**

ÊÇ®‰ªäÊó•ÂèØÈ¢ÜÂèñ ${dailyFlowers} ÊúµÂ∞èÁ∫¢Ëä±ÔºÅ

üí° ËÆ∞ÂæóÊØèÂ§©È¢ÜÂèñÔºå‰øùÊåÅËøûÁª≠Â•ñÂä±ËÆ∞ÂΩïÂì¶ÔΩû
`;

        await dailyRewardBot.telegram.sendMessage(user.telegram_username, reminderMessage, {
          parse_mode: 'Markdown',
          reply_markup: {
            inline_keyboard: [
              [{ text: 'üå∏ Á´ãÂç≥È¢ÜÂèñ', callback_data: 'claim_daily_reward' }]
            ]
          }
        });
        
        await new Promise(resolve => setTimeout(resolve, 200)); // ÈÅøÂÖçÈ¢ëÁéáÈôêÂà∂
        
      } catch (error) {
        console.error(`ÂèëÈÄÅÊèêÈÜíÁªô ${user.telegram_username} Â§±Ë¥•:`, error);
      }
    }
    
  } catch (error) {
    console.error('ÂèëÈÄÅÊØèÊó•ÊèêÈÜíÂ§±Ë¥•:', error);
  }
}

// ÈîôËØØÂ§ÑÁêÜ
dailyRewardBot.catch((err, ctx) => {
  console.error('Daily reward bot error:', err);
  ctx.reply('‚ùå ÂèëÁîüÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï');
});

export default dailyRewardBot;
